<template>
	<view>
		<view class="box">
			<view class="box1">
				<image src="../../static/image/shareimage.jpg" mode="" class="imagestyle"></image>
			</view>
			<view class="buttonview">
				<button type="default" class="buttonstyle" @click="saveImageClcik()">保存图片到相册</button>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {

			}
		},
		methods: {
			// 保存图片到本地
			saveImageClcik: function() {
				uni.chooseImage({
					count: 1,
					sourceType: ['camera'],
					success: function(res) {
						uni.saveImageToPhotosAlbum({
							filePath: res.tempFilePaths[0],
							success: function() {
								console.log('save success');
								uni.showToast({
									icon:'none',
									title:'保存图片成功'
								})
							}
						});
					}
				});
			}
		}
	}
</script>

<style lang="scss">
	.box {
		width: 100%;
		height: 100%;
		padding: 10px;
		box-sizing: border-box;

		.box1 {
			width: 100%;
			height: 600px;

			.imagestyle {
				width: 100%;
				height: 100%;
			}
		}

		.buttonview {
			width: 100%;
			height: 50px;
			margin-top: 20px;

			.buttonstyle {
				background-color: #f90004;
				color: #FFFFFF;
			}
		}
	}
</style>
